n  stroke: #b1b1b7;\n  stroke-width: 1;\n}\n\n.react-flow__edge-text {\n  font-size: 10px;\n}\n\n.react-flow__edge-textbg {\n  fill: white;\n}\n\n.react-flow__connection-path {\n  stroke: #b1b1b7;\n  stroke-width: 1;\n}\n\n.react-flow__node {\n  cursor: grab;\n}\n\n.react-flow__node-default,\n.react-flow__node-input,\n.react-flow__node-output {\n  padding: 10px;\n  border-radius: 3px;\n  width: 150px;\n  font-size: 12px;\n  color: #222;\n  text-align: center;\n  border-width: 1px;\n  border-style: solid;\n}\n\n.react-flow__node-default.selectable,\n.react-flow__node-input.selectable,\n.react-flow__node-output.selectable {\n  &:hover {\n    box-shadow: 0 1px 4px 1px rgba(0, 0, 0, 0.08);\n  }\n}\n\n.react-flow__node-input {\n  background: #fff;\n  border-color: #0041d0;\n\n  &.selected,\n  &.selected:hover {\n    box-shadow: 0 0 0 0.5px #0041d0;\n  }\n\n  .react-flow__handle {\n    background: #0041d0;\n  }\n}\n\n.react-flow__node-default {\n  background: #fff;\n  border-color: #1a192b;\n\n  &.selected,\n  &.selected:hover {\n    box-shadow: 0 0 0 0.5px #1a192b;\n  }\n\n  .react-flow__handle {\n    background: #1a192b;\n  }\n}\n\n.react-flow__node-output {\n  background: #fff;\n  border-color: #ff0072;\n\n  &.selected,\n  &.selected:hover {\n    box-shadow: 0 0 0 0.5px #ff0072;\n  }\n\n  .react-flow__handle {\n    background: #ff0072;\n  }\n}\n\n.react-flow__nodesselection-rect {\n  background: rgba(0, 89, 220, 0.08);\n  border: 1px dotted rgba(0, 89, 220, 0.8);\n}\n\n.react-flow__handle {\n  position: absolute;\n  width: 6px;\n  height: 6px;\n  background: #555;\n  border: 1px solid white;\n  border-radius: 100%;\n\n  &.connectable {\n    cursor: crosshair;\n  }\n}\n\n.react-flow__minimap {\n  background-color: #fff;\n}\n\n.react-flow__controls {\n  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.08);\n\n  &-button {\n    background: #fefefe;\n    border-bottom: 1px solid #eee;\n    box-sizing: content-box;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    width: 16px;\n    height: 16px;\n    cursor: pointer;\n    user-select: none;\n    padding: 5px;\n\n    svg {\n      max-width: 12px;\n      max-height: 12px;\n    }\n\n    &:hover {\n      background: #f4f4f4;\n    }\n  }\n}\n","@use \"color\" as *;\n\n@mixin node-position-absolute-top-right {\n  position: absolute;\n  top: 0px;\n  right: 2px;\n}\n\n@mixin border_line_basic {\n  border-width: 0px 0px 1px 0px;\n  border-style: solid;\n  border-color: $color-black;\n}\n\n.gu_flow_default_element {\n  .add {\n    @include node-position-absolute-top-right;\n    color: $color-bs-primary;\n  }\n  .chk {\n    @include node-position-absolute-top-right;\n    color: $color-green;\n  }\n  .border_line {\n    @include border_line_basic;\n    &.invalid {\n      border-color: $color-invalid;\n    }\n  }\n}\n\n/* index css */\n\n.overview-example__add {\n  display: none;\n}\n\n.react-flow__node a {\n  font-weight: 700;\n  color: #111;\n}\n\n.react-flow__node.dark-node {\n  background: #0041d0;\n  color: #f8f8f8;\n}\n\n.react-flow__node.dark {\n  background: #557;\n  color: #f8f8f8;\n}\n\n.react-flow__node-selectorNode {\n  font-size: 12px;\n  background: #f0f2f3;\n  border: 1px solid 555;\n  border-radius: 5px;\n  text-align: center;\n}\n\n.react-flow__node-selectorNode .react-flow__handle {\n  border-color: #f0f2f3;\n}\n\n@media screen and (min-width: 768px) {\n  .overview-example__add {\n    display: block;\n  }\n}\n\n/* DnDFlow css */\n\n.dndflow {\n  flex-direction: column;\n  display: flex;\n  flex-grow: 1;\n  height: 100%;\n}\n\n.dndflow aside > * {\n  margin-bottom: 10px;\n  cursor: grab;\n}\n\n.dndflow aside {\n  background: $color-background;\n  border-right: 1px solid $color-default;\n  flex-grow: 1;\n  font-size: 12px;\n  overflow: auto;\n  padding: 15px 10px;\n  max-width: 190px;\n}\n\n.dndflow aside .description {\n  margin-bottom: 10px;\n}\n\n.dndflow .dndnode {\n  height: 20px;\n  padding: 4px;\n  border: 1px solid #1a192b;\n  border-radius: 2px;\n  margin-bottom: 10px;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  // cursor: grab;\n  // background: white;\n}\n\n.dndflow .dndnode:hover {\n  box-shadow: 0 0 2.6px rgba($color: $color-selected-border, $alpha: 1.0);\n  border-color: $color-bs-info;\n}\n\n// .dndflow .dndnode.input {\n//   border-color: #0041d0;\n// }\n\n// .dndflow .dndnode.output {\n//   border-color: #ff0072;\n// }\n\n.dndflow .reactflow-wrapper {\n  flex-grow: 1;\n  height: 100%;\n}\n\n.dndflow .selectall {\n  margin-top: 10px;\n}\n\n@media screen and (min-width: 768px) {\n  .dndflow {\n    flex-direction: row;\n  }\n\n  .dndflow aside {\n    width: 20%;\n    max-width: 190px;\n  }\n}\n\n/* Provider css */\n\n.providerflow {\n  flex-direction: column;\n  display: flex;\n  height: 100%;\n}\n\n.providerflow aside {\n  border-right: 1px solid #eee;\n  padding: 15px 10px;\n  font-size: 12px;\n  background: #fcfcfc;\n}\n\n.providerflow aside .description {\n  margin-bottom: 10px;\n}\n\n.providerflow aside .title {\n  font-weight: 700;\n  margin-bottom: 5px;\n}\n\n.providerflow aside .transform {\n  margin-bottom: 20px;\n}\n\n.providerflow .reactflow-wrapper {\n  flex-grow: 1;\n  height: 100%;\n}\n\n.providerflow .selectall {\n  margin-top: 10px;\n}\n\n@media screen and (min-width: 768px) {\n  .providerflow {\n    flex-direction: row;\n  }\n\n  .providerflow aside {\n    width: 20%;\n    max-width: 250px;\n  }\n}\n\n/* save buttons */\n\n.save__controls {\n  position: absolute;\n  right: 10px;\n  top: 10px;\n  z-index: 4;\n  font-size: 12px;\n}\n\n.save__controls button {\n  margin-left: 5px;\n}\n","$color-green: green;\n$color-grey: grey;\n$color-lightcoral: lightcoral;\n$color-lightgray: lightgrey;\n$color-lightyellow: lightyellow;\n$color-red: red;\n$color-black: black;\n$color-white: white;\n\n$color-background: #eee;\n$color-bs-info: #5bc0de;\n$color-bs-primary: #337ab7;\n$color-bs-warning: #f0ad4e;\n$color-btn-success: $color-green;\n$color-btn-warning: $color-red;\n$color-can-drop: $color-lightgray;\n$color-default: #777;\n$color-editable: #fff;\n$color-invalid: $color-lightcoral;\n$color-is-over: #31708f;\n$color-readonly: #ddd;\n$color-required: $color-lightyellow;\n$color-selected-border: #0dcaf0;\n$color-attention-emphasis: #9a6700;\n$color-halloween: #fe9600;\n",".gu-full-modal {\n  width: 90vw;\n  height: 90vh;\n  max-width: none;\n  margin-left: auto;\n  margin-right: auto;\n  // top: 47%;\n\n  // .modal-dialog {\n  //   top: 47%;\n  // }\n\n  .modal-content {\n    height: 100%;\n    // padding: 10px;\n    // overflow: hidden;\n\n    .modal-body {\n      height: 92%;\n    }\n  }\n\n  ::-webkit-scrollbar {\n  background: transparent;\n  overflow: visible;\n  width: 17px;\n  }\n\n  ::-webkit-scrollbar-thumb {\n  background-color: rgba(0, 0, 0, 0.2);\n  border: solid #fff;\n  }\n\n  ::-webkit-scrollbar-thumb:hover {\n  background: rgba(0, 0, 0, 0.8);\n  }\n\n  ::-webkit-scrollbar-thumb:horizontal {\n  border-width: 4px 6px;\n  min-width: 40px;\n  }\n\n  ::-webkit-scrollbar-thumb:vertical {\n  border-width: 6px 4px;\n  min-height: 40px;\n  }\n\n  ::-webkit-scrollbar-track-piece {\n  background-color: #fff;\n  }\n\n  ::-webkit-scrollbar-corner {\n  background: transparent;\n  }\n\n  ::-webkit-scrollbar-thumb {\n  background-color: #ADADAD;\n  -webkit-box-shadow: inset 1px 1px 0 rgba(0,0,0,0.10),inset 0 -1px 0 rgba(0,0,0,0.07);\n  }\n\n  ::-webkit-scrollbar-thumb:hover {\n  background-color: #999;\n  }\n}\n\n.gu_modal-68w {\n  width: 68% !important;\n  max-width: none !important;\n}\n\n.gu-full-width-panel {\n  white-space: normal;\n  width: 100%;\n  // height: 100%;\n  padding: 5px;\n  border: 2px solid red;\n  border-style: ridge;\n  box-sizing: border-box;\n  background-color: #99999944;\n  font-family: inherit;\n}\n\n.gu-full-width-center {\n  // overflow-y: scroll;\n  border: 1px solid grey;\n  padding: 2px;\n  // height: 100%;\n  box-sizing: border-box;\n  font-family: cursive;\n  background-color: #99999944;\n  font-family: inherit;\n}\n","$gu-spacer: 1rem;\n$gu-spacers: (\n  0: 0,\n  1: $gu-spacer * .25,\n  2: $gu-spacer * .5,\n  3: $gu-spacer,\n  4: $gu-spacer * 1.5,\n  5: $gu-spacer * 3,\n);\n\n:root {\n  // Define the keys and margin types\n  @each $spacer-key, $value in $gu-spacers {\n    --gu-ml-#{$spacer-key}: #{$value};\n    --gu-mr-#{$spacer-key}: #{$value};\n    --gu-mb-#{$spacer-key}: #{$value};\n  }\n}\n","@use 'color' as *;\n@import 'mixins';\n\n.gu-ml-0 {\n  @include gux-ml(0);\n}\n\n.gu-ml-1 {\n  @include gux-ml(1);\n}\n\n.gu-ml-2 {\n  @include gux-ml(2);\n}\n\n.gu-ml-3 {\n  @include gux-ml(3);\n}\n\n.gu-ml-4 {\n  @include gux-ml(4);\n}\n\n.gu-mr-0 {\n  @include gux-mr(0);\n}\n\n.gu-mr-1 {\n  @include gux-mr(1);\n}\n\n.gu-mr-2 {\n  @include gux-mr(2);\n}\n\n.gu-mr-3 {\n  @include gux-mr(3);\n}\n\n.gu-mr-4 {\n  @include gux-mr(4);\n}\n.gu-mr-auto {\n  @include gux-mr(auto);\n}\n\n.gu-mb-0 {\n  @include gux-mb(0);\n}\n\n.gu-mb-1 {\n  @include gux-mb(1);\n}\n\n.gu-mb-2 {\n  @include gux-mb(2);\n}\n\n.gu-mb-3 {\n  @include gux-mb(3);\n}\n\n.gu-mb-4 {\n  @include gux-mb(4);\n}\n\n.btn-gxs,\n.btn-group-gxs > .btn {\n  padding: 1px 5px;\n  font-size: 12px;\n  line-height: 1.5;\n  border-radius: 3px;\n}\n\n.gu-no-pointer {\n  cursor: default;\n  text-decoration: none;\n}\n\n.gu-no-pointer:hover {\n  text-decoration: none;\n}\n\n.gu-ont-display {\n  @include gux-ml(2);\n  background-color: $color-green !important;\n  color: $color-white !important;\n  border: $color-green solid 1px;\n}\n\n.gu-ont-display:hover {\n  border-color: $color-green !important;\n}\n\n// after upgrade to react-bootstrap 2.10\n.gu-menu-item-del {\n  color: $color-red !important;\n\n  &:hover,\n  &:focus,\n  &:active {\n    color: $color-white !important;\n    background-color: $color-red !important;\n  }\n}\n\n.gu-menu-item-cond {\n  color: $color-halloween !important;\n\n  &:hover,\n  &:focus,\n  &:active {\n    color: $color-white !important;\n    background-color: $color-halloween !important;\n  }\n}\